.register {
    background:transparent ;
    background-size: cover;
    background-attachment: fixed;
    margin-bottom: -100px;
    .left_part {
        position: fixed;
        left: 0;
        top: 0;
        height: 100vh;
        width: inherit;
        background-repeat: no-repeat;
        background-size: cover;
        color: white;
    }
    .right_part {
        padding: 60px 0;
    }
}

.login {
    height: 100vh;
    margin-bottom: -100px;
    @include tablet_vert_screen {
        height: auto;
    }
}
